.Pdata {
    &__container {
        display: grid;
        grid: auto/ 1.5fr 1fr;
        gap: 5rem;
        margin-bottom: 11rem;
        margin-inline: 1rem;
        line-height: 1.2;
        img {
            width: 100%;
        }
        @media (max-width: 1250px) {
            grid: auto/ 1fr;
        }
        @media (max-width: 980px) {
            margin-bottom: 7rem;
        }
    }
    &__container-child {
        padding: 2rem 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
    }
    &__container-btns {
        display: flex;
        align-items: center;
        color: white;
        @media (max-width: 400px) {
            display: flex;
            align-items: flex-start;
            color: white;
            flex-direction: column;
        }

        button {
            background-color: var(--hover-sky-blue);
            @media (max-width: 991.98px) {
                border: 0;
            }
        }
        .btntwo {
            background-color: transparent;
            display: flex;
            text-decoration: none;
            color: var(--hover-sky-blue);
            align-items: center;
            gap: 11px;
            border: 0;
            border-radius: 3rem 1rem 3rem 1rem;
            box-shadow: none;
            padding-inline: 1rem;
            font-family: "Source Sans Pro", sans-serif;
            font-size: 2rem;
            letter-spacing: 2px;
            font-weight: 600;
            @media (max-width: 768px) {
                font-size: 1rem;
            }

            &:hover,
            &:focus {
                & *:nth-child(2) {
                    color: black;
                }
            }
            & *:nth-child(2) {
                color: white;
                &:hover,
                &:focus {
                    color: black;
                }
            }
            & *:nth-child(1) {
                font-size: 2.2rem;
            }
        }
        a:nth-child(1) {
            font-size: 1.4rem;
            color: white;
            text-decoration: none;
            text-transform: uppercase;
            letter-spacing: 4px;
            font-weight: 500;
            @media (max-width: 991.98px) {
                font-size: 1rem;
            }
        }
        a:nth-child(2) {
            margin: 0rem 0rem 0rem 2rem;
            font-size: 1.4rem;
            text-decoration: none;
            letter-spacing: 3px;
            @media (max-width: 400px) {
                margin: 1rem 0rem 0rem 0rem;
            }
            &:active {
                scale: 0.91;
            }
        }
    }
    &__container-disc {
        color: rgb(148, 148, 148);
        line-height: 1.3;
        font-size: 1.3rem;
    }
    &__container-skills {
        display: flex;
        flex-wrap: wrap;
        gap: 1.5rem;
        margin-bottom: 1rem;
    }
    &__container-skills-childs {
        background: white;
        border-radius: 99999px;
        color: #92c9cc;
        font-size: 1rem;
        font-weight: 600;
        padding: 0.5rem 1.5rem;
        width: -webkit-min-content;
        width: min-content;
        border: 3px dotted white;
        box-shadow: 0px 0px 0px 4px #92c9cc;
        letter-spacing: 1px;
        @media (max-width: 991.98px) {
            font-size: 0.9rem;
        }
    }
    &__container-title {
        font-weight: 700;
    }
}
